/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import SEO from "./utils/seo"
import Debug from "./utils/debug"
// import { useIntl } from "gatsby-plugin-intl"
import { useTranslations } from 'gatsby-plugin-translate'

import Header from "./nav/header"
import Footer from "./nav/footer"

const Layout = ({ pageContext, children }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery2 {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  const t = useTranslations(); //useIntl().formatMessage;

  return (
    <>
      {/* <Debug>{JSON.stringify(pageContext)}</Debug> */}
      <SEO title={pageContext.title} description={pageContext.description} />
      <Header siteMeta={data.site.siteMetadata} pageContext={pageContext} />
      <div>
        {children}
      </div>
      <Footer />
    </>
  )
}

Layout.propTypes = {
  pageContext: PropTypes.object.isRequired,
}

Layout.defaultProps = {
  pageContext: {},
}

export default Layout
