import React from "react"
import { Link } from "gatsby"
import PropTypes from "prop-types"

import data from "./footer.json"
import Image from "../image"

const Footer = ({ siteTitle }) => {
  const { description, nav_primary, nav_secondary } = data;

  return (
    <footer>
      <div style={{ display: `flex`}}>
        <div style={{ width: `150px`}}>
          <Image src="gatsby-astronaut.png" />
        </div>
        <div>
          <p>{description}</p>
        </div>
        <div>
          <ul>
            {nav_secondary.map((link, index) => (
              <li key={index}><Link to={link.src}>{link.label}</Link></li>
            ))}
          </ul>
        </div>
        <div>
          <ul>
            {nav_primary.map((link, index) => (
              <li key={index}><Link to={link.src}>{link.label}</Link></li>
            ))}
          </ul>
        </div>
      </div>
      <div>
      Aristo Wealth Management © {new Date().getFullYear()} All Rights Reserved
      </div>
    </footer>
  )
}

Footer.propTypes = {
  siteTitle: PropTypes.string,
}

Footer.defaultProps = {
  siteTitle: ``,
}

export default Footer