import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import data from "./header.json";
import services from "../../data/services.json"

const Header = ({ siteMeta, pageContext }) => {
  const { nav } = data;

  // Add services sub links
  nav.services.children = Object.values(services.data).map((page) => {
    return {
      label: page.title,
      src: page.path
    }
  })

  // Recursively Build Nav
  const createNavSection = (section) => (
    <ul>
      {section.map((link, index) => (
          <li key={index}>
            <Link to={link.src}>{link.label}</Link>
            {link.children && link.children.length > 0 && (
              createNavSection(link.children)
            )}
          </li>
      ))}
    </ul>
  )

  return (
    <header
      style={{
        background: `#161616`,
        marginBottom: `1.45rem`,
      }}
    >
      <div>
        <h1>
          <Link
            to="/"
            style={{
              color: `white`,
              textDecoration: `none`,
            }}
          >
            {siteMeta.title || `Title`}
          </Link>
        </h1>
      </div>
      <nav>
        {createNavSection(Object.values(nav))}
      </nav>
    </header>
  )
}

Header.propTypes = {
  siteMeta: PropTypes.object,
  pageContext: PropTypes.object,
}

Header.defaultProps = {
  siteMeta: {},
  pageContext: {},
}

export default Header
